<template>
<div>
<el-row>
  <el-col :span="6" v-for="pic in data" :key="pic.id">
    <el-card :body-style="{ padding: '10px' }">
      <img :src=pic.url class="image">
      <div style="padding: 14px;">
        <span>{{pic.name}}</span>
        <div class="bottom clearfix">
          <div class="time">{{ pic.date }}</div>
          <div class="time">{{ pic.size }}</div>
          <el-button type="text" class="button">操作按钮</el-button>
        </div>
      </div>
    </el-card>
  </el-col>
</el-row>
</div>
  
</template>

<style>
  .time {
    font-size: 13px;
    color: #999;
  }
  
  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  
  .clearfix:after {
      clear: both
  }
</style>

<script>
export default {
  data() {
    return {
      currentDate: new Date(),
      data:[
        {id:1,url:'./res/img/1.jpg',name:'那年今天',size:'2.3MB',date:'2018-12-23 10:23:32'},
        {id:2,url:'./res/img/2.jpg',name:'那年今天',size:'2.3MB',date:'2018-12-23 10:23:32'},
        {id:3,url:'./res/img/logo.png',name:'那年今天',size:'2.3MB',date:'2018-12-23 10:23:32'},
        {id:4,url:'./res/img/1.jpg',name:'那年今天',size:'2.3MB',date:'2018-12-23 10:23:32'},
        {id:5,url:'./res/img/1.jpg',name:'那年今天',size:'2.3MB',date:'2018-12-23 10:23:32'}
      ]
    };
  }
}
</script>



