import Vue from 'vue'
import Router from 'vue-router'
import photo_list from '@/photo/list.vue'
import video_list from '@/video/list.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/photo',
      name: 'photo_list',
      component: photo_list
    },
    {
      path: '/video',
      name: 'video_list',
      component: video_list
    }
  ]
})



